import { Component, OnInit } from '@angular/core';
import { ActivatedRoute,Router } from '@angular/router';
import {DataService} from './../data.service';
@Component({
  selector: 'app-skills-show',
  templateUrl: './skills-show.component.html',
  styleUrls: ['./skills-show.component.scss']
})
export class SkillsShowComponent implements OnInit {
  paramid:string;
  content:string;
  public skillsData:any=[];
  public products:any=[];
  constructor(
    private route: ActivatedRoute,
    private dataservice:DataService,
    private router:Router
  ) { }

  ngOnInit() {
    this.paramid=this.route.snapshot.params.id;
    this.dataservice.AllSkillGet().subscribe(data=>{
      this.skillsData=data;
      this.skillsData.forEach((product)=>{
          if(this.paramid==product.id){
            this.products.push(product);
            console.log(this.products);
          }
      });
    });
  }

  previous(){
    this.router.navigate(['skills']);
  }
}
