import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import {environment} from '../environments/environment';
import {Router } from '@angular/router';

import {contactData} from './dataInterface';
import { Observable } from 'rxjs';
import {UserInterface} from './user-interface';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor(
      private http:HttpClient,
      private router:Router
  ) { }


  dataUrl:string=environment.DataUrl+'/Profiles';
  contactUrl:string=environment.DataUrl+'/Enquiries';
  skillsUrl:string=environment.DataUrl+'/Skills';
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type'  :  'application/json; charset=utf-8',
      //'x-access-token' : this.authtoken
    })
  };
  userList():Observable<UserInterface[]>{
    return this.http.get<UserInterface[]>(this.dataUrl);
  }
  userListById(userId:string):Observable<UserInterface>{
    return this.http.get<UserInterface>(this.dataUrl+"/"+userId);
  }
  createUserList(userList:UserInterface):Observable<UserInterface>{
    let httpHeaders=new HttpHeaders().set('content-type','application/json; charset=utf-8');
    let options={
      headers:httpHeaders
    };
    return this.http.post<UserInterface>(this.dataUrl,userList,options);
  }
  updateUserList(userList:UserInterface):Observable<number>{
    let httpHeaders=new HttpHeaders().set('content-type','application/json; charset=utf-8');
    let options={
      headers:httpHeaders
    };
    return this.http.put<number>(this.dataUrl+"/"+userList.id,userList,options);
  }
  deleteUserList(userList:UserInterface):Observable<number>{
    alert(userList);
    let httpHeaders=new HttpHeaders().set('content-type','application/json; charset=utf-8');
    let options={
      headers:httpHeaders
    };
    return this.http.delete<number>(this.dataUrl+"/"+userList);
  }

  AllSkillGet(){
    return this.http.get(this.skillsUrl);
  }

  postContactForm(contactData:contactData):Observable<contactData>{
    console.log("All record is service page =",contactData);
    return this.http.post<contactData>(this.contactUrl,contactData,this.httpOptions);
  }

}
