import { Component, OnInit } from '@angular/core';
import {DataService} from './../data.service';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { from, Observable } from 'rxjs';
import {environment} from '../../environments/environment';
import {contactData} from './../dataInterface';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  dataUrl:string=environment.DataUrl;
  name:string;
  email:string;
  subject:string;
  message:string;  
  
  

  constructor(
    private dataservice:DataService,
    private http:HttpClient
  ) { }

  ngOnInit() {
    
 

  }



  submitRecord(){
    let allRecord={
      name:this.name,
      email:this.email,
      subject:this.subject,
      message:this.message
    }
    this.contactFormSubmit(allRecord);
  }

  contactFormSubmit(contactData:contactData){
    console.log("All record is app page =",contactData);
    this.dataservice.postContactForm(contactData).subscribe(data=>{
      //console.log("contact for submit",data);
    });
  }

  
}
