import { Component, OnInit } from '@angular/core';
import {DataService} from './../data.service';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { from, Observable } from 'rxjs';
import {environment} from '../../environments/environment';
import {FormBuilder,Validators} from '@angular/forms';

import {UserInterface} from '../user-interface';

@Component({
  selector: 'app-userlist',
  templateUrl: './userlist.component.html',
  styleUrls: ['./userlist.component.scss']
})
export class UserlistComponent implements OnInit {
  dataUrl:string=environment.DataUrl;
  public userList: any = [];
  searchText:string;
  noData:string;
  dataSaved:false;
  userlistForm:any;
  allUserList:Observable<UserInterface[]>;
  userIdToUpdate=null;
  message=null;


  constructor(
    private dataservice:DataService,
    private http:HttpClient,
    private formbuilder:FormBuilder
    ) { }

  ngOnInit() {
    this.userlistForm=this.formbuilder.group({
      name:['',[Validators.required]],
      email:['',[Validators.required]],
      message:['',[Validators.required]]
    });
    this.userListShow();
  }
  userListShow(){
    this.dataservice.userList().subscribe(data=>{
      console.log(data);
      this.userList=data;
    });  
  };

  onFormSubmit(){
    this.dataSaved=false;
    let users=this.userlistForm.value;
    this.createUsers(users);
    this.userlistForm.reset();
  }

  createUsers(users:UserInterface){
    if(this.userIdToUpdate==null){
      this.dataservice.createUserList(users).subscribe(
        ()=>{
          //this.dataSaved=true;
          this.message="Record saved Successfully";
           this.userListShow();
           this.userIdToUpdate=null;
           this.userlistForm.reset();       
        });
    }
    else{
      users.id=this.userIdToUpdate;
      this.dataservice.updateUserList(users).subscribe(
        ()=>{
          //this.dataSaved=true;
          this.message="Record update successfully";
          this.userListShow();
          this.userIdToUpdate=null;
          this.userlistForm.reset();   
        });
    }
  } 


  editUser(userId:string){
    this.dataservice.userListById(userId).subscribe(users=>{
      this.message=null;
      this.dataSaved=false;
      this.userIdToUpdate=users.id;
      this.userlistForm.controls['name'].setValue(users.name);
      this.userlistForm.controls['email'].setValue(users.email);
      this.userlistForm.controls['message'].setValue(users.message);
    });
  }
  deleteUser(userId:UserInterface){
    this.dataservice.deleteUserList(userId).subscribe(
      ()=>{
        this.message="Record deleted successfully";
        this.userListShow();
        this.userIdToUpdate=null;
        this.userlistForm.reset();
      }
    )
  }

  resetForm(){
    this.userlistForm.reset();
    this.message=null;
    this.dataSaved=false;
  }










  search(){
    console.log( this.userList);
    if(this.searchText !=""){
      this.userList=this.userList.filter(res=>{
        return res.name.toLocaleLowerCase().match(this.searchText.toLocaleLowerCase());
      });
    }
    else if(this.searchText==""){
      this.ngOnInit();
    }
    else{
      this.noData="Not Data available";
    }
    
  }
}
